.burger {
  cursor: pointer;
  width: 25px;
  height: 16px;
  line-height: 1;
  position: relative;
  transition: all 0.25s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  margin-right: 22px;
}

.burger__inner {
  margin-bottom: -3px;
}

.burger__line {
  width: 25px;
  height: 2px;
  background-color: #616161;
  position: relative;
  transition: all 0.25s ease-in-out;
  margin-bottom: 5px;
}

.burger__line_1 {
  transition: all 0.25s ease-in-out;
}

.burger__line_2 {
  transition: all 0.25s ease-in-out;
}

.burger__line_3 {
  transition: all 0.25s ease-in-out;
}

.burger__line_4 {
  transition: all 0.25s ease-in-out;
}

.burger.open .burger__line_1 {
  transform: translateY(8px) rotate(45deg);
}

.burger.open .burger__line_2 {
  opacity: 0;
}

.burger.open .burger__line_3 {
  opacity: 0;
}

.burger.open .burger__line_4 {
  transform: translateY(-7px) rotate(-45deg);
}